import React, {Component} from 'react';
 
class Country extends Component {

  

  constructor (props) {
    super(props);

    this.state = {
      results: [], 
      received: false
    }
  }

  async search(){

    const country = this.props.countryName;
    console.log("CDM " + country);

    if(country != null) {
      const response = await fetch("https://disease.sh/v2/countries/" + country);
      const data = await response.json();
      console.log(data);
      this.setState({results: data, received: true});
      console.log(this.state.results);
    }
  } 
  
 
  render () {

    this.search();

    console.log(this.props.countryName);
    
    return (
      <div>
        { this.state.received ?
            <div>
              <table className = "countrySearchTable">
                        <thead>
                            <tr>
                                <th colSpan='4' className = "table-heading"> {this.props.countryName} </th>
                            </tr>
                            <tr>
                                <td>CASES</td>
                                <td>ACTIVE</td>
                                <td>DEATHS</td>
                                <td>RECOVERED</td>
                                </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td id="total">{this.state.results.cases}</td>
                                <td id="active">{this.state.results.active}</td>
                                <td id="deaths">{this.state.results.deaths}</td>
                                <td id="recover">{this.state.results.recovered}</td>
                            </tr>
                        </tbody>
                        <thead>
                            <tr>
                                <td colSpan='2' className = "innerTable">TODAY CASES</td>
                                <td colSpan='2' className = "innerTable">TODAY DEATHS</td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td id = "names" colSpan='2'>{this.state.results.todayCases}</td>
                                <td id = "names" colSpan='2'>{this.state.results.todayDeaths}</td>
                            </tr>
                        </tbody>
                    </table>
            </div>
            :
            <div>
            </div>
        }
      </div>
    );
  }
}

export default Country;