import React, { Component } from 'react';
import India from './India';
import StateGraph from './StateGraph';
import StateTable from './StateTable';
import SingleState from './SingleState';
import { animateScroll as scroll } from 'react-scroll';

class Home extends Component{

  state = {
    results: [],
    received: false,
    selectedState: null
  };

  constructor(props){
    super(props);
    

    this.onStateSelect = this.onStateSelect.bind(this);
    this.updateState = this.updateState.bind(this);
  }

  async fetchFirst(){

    const response = await fetch("https://api.covid19india.org/data.json");
    const data = await response.json();
    console.log(data);
    this.setState({results: data, received: true, selectedState: null});
    console.log(this.state.results);

  }

  componentDidMount(){
    this.fetchFirst();
  }

  onStateSelect(stateName){

    this.setState({ selectedState: stateName });

    if(window.innerWidth <= 500){
      scroll.scrollTo(4000);
    } else if(window.innerWidth > 500 && window.innerWidth <= 900){
        scroll.scrollTo(3600)
    } else {
      scroll.scrollTo(2000);
    }

    console.log("select state home" + this.state.selectedState);
    console.log("screen length " + window.innerWidth);
  }

  updateState(){
    this.state.selectedState = null;
  }

  renderTop(){
    let table = [];
    
    for(let i = 0; i < this.state.results.statewise.length; i++){
      if(this.state.results.statewise[i].state === this.state.selectedState){
        
        let row = [];

        row.push(<td id="total">{this.state.results.statewise[i].confirmed}</td>);
        row.push(<td id="active">{this.state.results.statewise[i].active}</td>);
        row.push(<td id="deaths">{this.state.results.statewise[i].deaths}</td>);
        row.push(<td id="recover">{this.state.results.statewise[i].recovered}</td>);
                    
        table.push(<tr>{row}</tr>);

        break;
      }
    }

    return table;
  }

  renderDown(){
    let table = [];

    for(let i = 0; i < this.state.results.statewise.length; i++){
      if(this.state.results.statewise[i].state === this.state.selectedState){
        
        let row = [];

        row.push(<td colSpan = "2" id = "names">{this.state.results.statewise[i].deltaconfirmed}</td>);
        row.push(<td colSpan = "2" id = "names">{this.state.results.statewise[i].deltadeaths}</td>);
                    
        table.push(<tr>{row}</tr>);

        break;
      }
    }

    return table;
  }


  render(){

    const data = this.state.results.statewise;

    return (
      <div className="App-link">
        {
          this.state.received ? 
          <div className="container-fluid mt-3">

            <India country = {this.state.results.statewise[0]}/>
            
            <div>

              <div className="row">
                <div className="col-md-6">
                  <StateTable stateData = {this.state.results.statewise} onStateSelect = {this.onStateSelect} /> 
                </div>
                <div className="col-md-6">
                  <StateGraph oldData = {this.state.results.cases_time_series} />
                </div>
              </div>

              {
                this.state.selectedState == null ?
                  <div> </div>
                  :
                  <div>

                    <table className = "mt-5" id = "state-table">
                        <thead>
                            <tr>
                              <th colSpan='4' className = "table-heading">{this.state.selectedState}</th>
                            </tr>
                            <tr>
                                <td>CASES</td>
                                <td>ACTIVE</td>
                                <td>DEATHS</td>
                                <td>RECOVERED</td>
                            </tr>
                        </thead>

                        <tbody>
                           
                          {this.renderTop()}
                       
                        </tbody>

                        <thead>
                          <tr>
                            <td colSpan='2' className = "innerTable">TODAY CASES</td>
                            <td colSpan='2' className = "innerTable">TODAY DEATHS</td>
                          </tr>
                        </thead>

                        <tbody>
                       
                          {this.renderDown()}
                       
                        </tbody>
                      </table>
                        
                      <SingleState selectedState = {this.state.selectedState} stateData = {data}  />

                      {this.updateState()}

                  </div> 
              }
            </div>
          </div>
          :
          <div className="loading"> Loading...  <i className="fa fa-spinner fa-spin" ></i> </div>
        }
      </div>
   
    );
  }
    
}

export default Home;
