import React, { Component } from "react";
import { Button, UncontrolledPopover, PopoverHeader, PopoverBody } from 'reactstrap';

class Footer extends Component{

    constructor(props) {
        super(props);

        this.RiyaLinkedIn = this.RiyaLinkedIn.bind(this);
        this.RiyaGithub = this.RiyaGithub.bind(this);
        this.RiyaMail = this.RiyaMail.bind(this);
        this.VibhushitLinkedIn = this.VibhushitLinkedIn.bind(this);
        this.VibhushitGithub = this.VibhushitGithub.bind(this);
        this.VibhushitMail = this.VibhushitMail.bind(this);
    }

    RiyaLinkedIn () {
        window.open("https://www.linkedin.com/in/riya242k", "_blank");
    }

    RiyaGithub () {
        window.open("https://github.com/riya242k", "_blank");
    }

    RiyaMail () {
        window.location.href = `mailto:riya171671.cse@chitkara.edu.in`;
    }

    VibhushitLinkedIn () {
        window.open("https://www.linkedin.com/in/vibhushit-singhal", "_blank");
    }

    VibhushitGithub () {
        window.open("https://github.com/Vibhushit07", "_blank");
    }

    VibhushitMail () {
        window.location.href = `mailto:vibhushitsinghal80@gmail.com`;
    }

    render(){
        return(
            <nav className = "footer bg-dark">
                Made by <span >      </span>
               <Button id="riya" type="button" >Riya Gupta   </Button> and <span >     </span>
               <Button id="vibhushit" type="button">   Vibhushit Singhal</Button>

                <UncontrolledPopover trigger="legacy" placement="bottom" target="riya">
                    <PopoverHeader>Contact Us</PopoverHeader>
                        <PopoverBody>

                            <span className = "fa fa-linkedin fa-lg ml-2"  onClick = { this.RiyaLinkedIn } /> 
                            <span className = "fa fa-github fa-lg ml-2"  onClick = { this.RiyaGithub } /> 
                            <span className = "fa fa-envelope fa-lg ml-2"  onClick = { this.RiyaMail } /> 
                        
                        </PopoverBody>
                </UncontrolledPopover>

                <UncontrolledPopover trigger="legacy" placement="bottom" target="vibhushit">
                    <PopoverHeader>Contact Us</PopoverHeader>
                        <PopoverBody>
                            <span className = "fa fa-linkedin fa-lg ml-2"  onClick = { this.VibhushitLinkedIn } /> 
                            <span className = "fa fa-github fa-lg ml-2"  onClick = { this.VibhushitGithub } /> 
                            <span className = "fa fa-envelope fa-lg ml-2"  onClick = { this.VibhushitMail } /> 
                        </PopoverBody>
                </UncontrolledPopover>
            </nav>
        );
    }

}

export default Footer;