import React, {Component} from 'react';

class India extends Component{
    constructor(props){
        super(props);
        this.state = {
            results: [],
            received: false
        };
    }

    async fetchFirst(){
        const response = await fetch("https://api.covid19india.org/data.json");
        const data = await response.json();
        console.log(data);
        this.setState({results: data, received: true});
        console.log(this.state.results);
    }

    componentDidMount(){
        this.fetchFirst();
    }
    render() {
        return(
            <div>
                { this.state.received ?
                    <table className = "india">
                        <thead>
                            <tr>
                                <th colSpan='4' className = "table-heading">INDIA</th>
                            </tr>
                            <tr>
                                <td>CASES</td>
                                <td>ACTIVE</td>
                                <td>DEATHS</td>
                                <td>RECOVERED</td>
                            </tr>
                        </thead>

                        <tbody>
                            <tr>
                                <td id="total">{this.state.results.statewise[0].confirmed}</td>
                                <td id="active">{this.state.results.statewise[0].active}</td>
                                <td id="deaths">{this.state.results.statewise[0].deaths}</td>
                                <td id="recover">{this.state.results.statewise[0].recovered}</td>
                            </tr>
                        </tbody>
                        
                        <thead>
                            <tr>
                                <td colSpan='2' className="innerTable">TODAY CASES</td>
                                <td colSpan='2' className="innerTable">TODAY DEATHS</td>
                            </tr>
                        </thead>

                        <tbody>
                            <tr>
                                <td id = "names" colSpan='2'>{this.state.results.statewise[0].deltaconfirmed}</td>
                                <td id = "names" colSpan='2'>{this.state.results.statewise[0].deltadeaths}</td>
                            </tr>
                        </tbody>
                    </table>
                    :
                    <div> Loading </div>
                }
            </div>
            
        );
    }
}
export default India;