import React, {Component} from 'react';

class WorldTable extends Component{

    render() {

        let active = this.props.Global.TotalConfirmed - (this.props.Global.TotalDeaths + this.props.Global.TotalRecovered);

        return(
            <div>
                
                    <table className = "global-table">
                        <thead>
                            <tr>
                                <th colSpan='4' className = "table-heading"> GLOBAL </th>
                            </tr>
                            <tr>
                                <td>CASES</td>
                                <td>ACTIVE</td>
                                <td>DEATHS</td>
                                <td>RECOVERED</td>
                                </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td id="total">{this.props.Global.TotalConfirmed}</td>
                                <td id="active">{active}</td>
                                <td id="deaths">{this.props.Global.TotalDeaths}</td>
                                <td id="recover">{this.props.Global.TotalRecovered}</td>
                            </tr>
                        </tbody>
                        <thead>
                            <tr>
                                <td colSpan='2' className = "innerTable">TODAY CASES</td>
                                <td colSpan='2' className = "innerTable">TODAY DEATHS</td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td id = "names" colSpan='2'>{this.props.Global.NewConfirmed}</td>
                                <td id = "names" colSpan='2'>{this.props.Global.NewDeaths}</td>
                            </tr>
                        </tbody>
                    </table>
                
            </div>
        );
    }
}
export default WorldTable;