import React, { Component } from "react";
import WorldTable from "./WorldTable";
import WorldSearch from "./WorldSearch";

class World extends Component {

    state = {
        data : [],
        received: false
    }

    async componentDidMount(){
        const response = await fetch("https://api.covid19api.com/summary");
        const data = await response.json();
        console.log(data);
        var active = data.Global.TotalConfirmed - (data.Global.TotalDeaths + data.Global.TotalRecovered);
        this.setState({results: data, active: active, received: true});
        console.log(this.state.results);
    }

    render(){
        return(
            <div className="App-link">
                {
                    this.state.received ?
                    <div className = "mt-3">
                        <WorldTable Global = {this.state.results.Global} />
                        <WorldSearch />
                    </div>
                    :
                    <div className="loading"> Loading...  <i className="fa fa-spinner fa-spin" ></i> </div>
                }
            </div>
        );
    }
}

export default World;