import React, {Component} from 'react';

class SingleState extends Component{

    state = {
        data : [],
        loading : false, 
        index: null
    }

    async componentDidMount(){
        const response = await fetch("https://api.covid19india.org/v2/state_district_wise.json");
        const data = await response.json();

        this.setState({data: data, loading: true});
        console.log(this.state.index);

    }

    renderTop(stateData, selectedState){

        let table = [];
        let row = [];
        let i = 0;

        for(i = 0; i < this.stateData.length; i++){
            if(stateData[i].state === selectedState){
                break;
            }else{
                i++;
            }
        }

        row.push(<td id = "total">{stateData[i].confirmed}</td>);
        row.push(<td>{stateData[i].active}</td>);
        row.push(<td>{stateData[i].deaths}</td>);
        row.push(<td>{stateData[i].recovered}</td>);

        table.push(<tr> {row} </tr>);

        return table;

    }

    renderTable(selectedState){

        let table = [];

        for(let i = 0; i < this.state.data.length; i++) {
            
            if(this.state.data[i].state === selectedState){

                for(let j = 0; j < this.state.data[i].districtData.length; j++){
                    
                    let row = [];

                    row.push(<td>{this.state.data[i].districtData[j].district}</td>);
                    row.push(<td>{this.state.data[i].districtData[j].active}</td>);
                    row.push(<td>{this.state.data[i].districtData[j].confirmed}</td>);
                    row.push(<td>{this.state.data[i].districtData[j].deceased}</td>);
                    row.push(<td>{this.state.data[i].districtData[j].recovered}</td>);
                    table.push(<tr key = {this.state.data[i].districtData[j].district}>{row}</tr>);
                }
                break;
            }
        }

        return table;
        
    }

    render() {

        console.log("state length"+this.props.stateData.length);
        
        return (
            
                <div className="App-link">
                    {this.state.loading ?
                        <table className = "singleState mt-5">
                            <thead>
                                <tr>
                                    <th id = "names">District</th>
                                    <th id = "active">Active Cases</th>
                                    <th id = "total">Confirmed Cases</th>
                                    <th id = "deaths">Total Deaths</th>
                                    <th id = "recover">Recovered</th>
                                </tr>
                            
                            </thead>
                            <tbody>
                                {this.renderTable(this.props.selectedState)}
                            </tbody>
                        </table>
                        :
                        <div>
                            Loading
                        </div>
                    }
                </div>
                

        )
    }
}

export default SingleState;
