import React, {Component} from 'react';
import doubts from '../assets/doubt.PNG';
import hand from '../assets/washing-hands.jpg';
import distance from '../assets/social-distance.png';
import mask from '../assets/cover-face.jpg';
import sneeze from '../assets/sneeze.jpg';
import cleaning from '../assets/house-cleaning.jpeg';
import health from '../assets/monitor-health.jpg';

class Precautions extends Component{

    render(){

        return(

            <div className = "main">
                <div className = "col-md-12">
                    <div className = "border-0 mb-3">
                        <div className = "card-body ">
                            <div className = "row">
                                <div className = "card-icon pt-2 d-md-block">
                                    {
                                        window.innerWidth <= 500 ?
                                            <img src = { doubts } className = "icons" />
                                        :
                                        window.innerWidth > 500 && window.innerWidth <= 950 ?
                                            <img src = { doubts } className = "iconm" />
                                        :
                                            <img src = { doubts } className = "iconl" />
                                    }
                                </div>
                                <div className = "col">
                                    <h3 className = "card-title h3 mb-2 text-left">Know how it spreads</h3>
                                    <ul>
                                        <li>There is currently no vaccine to prevent coronavirus disease 2019 (COVID-19).</li>
                                        <li><strong>The best way to prevent illness is to avoid being exposed to this virus.</strong>
                                        </li>
                                        <li>The virus is thought to spread mainly from person-to-person.
                                            <ul>
                                                <li>Between people who are in close contact with one another (within about 6 feet).</li>
                                                <li>Through respiratory droplets produced when an infected person coughs, sneezes or talks.</li>
                                                <li>These droplets can land in the mouths or noses of people who are nearby or possibly be inhaled into the lungs.</li>
                                                <li>Some recent studies have suggested that COVID-19 may be spread by people who are not showing symptoms.</li>
                                            </ul>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <h1 className = "text-center"> Everyone Should </h1>

                <div className = "row ">
                    <div className = "col-md-12">
                        <div className = "border-0 mb-3">
                            <div className = "card-body ">
                                <div className = "row">
                                    <div className = "card-icon pt-2 d-md-block ml-3">
                                    {
                                        window.innerWidth <= 500 ?
                                            <img src = { hand } className = "icons" />
                                        :
                                        window.innerWidth > 500 && window.innerWidth <= 950 ?
                                            <img src = { hand } className = "iconm" />
                                        :
                                            <img src = { hand } className = "iconl" />
                                    }
                                    </div>
                                    <div className = "col">
                                        <h3 className = "card-title h3 mb-2 text-left">Wash your hands often</h3>
                                        <ul>
                                            <li>Wash your hands often with soap and water for at least 20 seconds especially after you have been in a public place, or after blowing your nose, coughing, or sneezing.</li>
                                            <li>If soap and water are not readily available, <strong>use a hand sanitizer that contains at least 60% alcohol</strong>. Cover all surfaces of your hands and rub them together until they feel dry.</li>
                                            <li><strong>Avoid touching your eyes, nose, and mouth</strong> with unwashed hands.</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className = "row ">
                    <div className = "col-md-12">
                        <div className = "border-0 mb-3">
                            <div className = "card-body ">
                                <div className = "row">      
                                    <div className = "card-icon pt-2 d-md-block ml-3">
                                    {
                                        window.innerWidth <= 500 ?
                                            <img src = { distance } className = "icons" />
                                        :
                                        window.innerWidth > 500 && window.innerWidth <= 950 ?
                                            <img src = { distance } className = "iconm" />
                                        :
                                            <img src = { distance } className = "iconl" />
                                    }
                                    </div>
                                    <div className = "col">
                                        
                                        <h3 className = "card-title h3 mb-2 text-left">Avoid close contact</h3>
                                        
                                        <ul>
                                            <li><strong>Avoid close contact with people who are sick, even inside your home.</strong> If possible, maintain 6 feet between the person who is sick and other household members.</li>
                                            <li><strong>Put distance between yourself and other people outside of your home</strong>.
                                                <ul>
                                                    <li>Remember that some people without symptoms may be able to spread virus.</li>
                                                    <li>Stay at least 6 feet (about 2 arms length) from other people.</li>
                                                    <li>Do not gather in groups.</li>
                                                    <li>Stay out of crowded places and avoid mass gatherings.</li>
                                                    <li>Keeping distance from others is especially important for people who are at higher risk of getting very sick.</li>
                                                </ul>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className = "row ">
                    <div className = "col-md-12">
                        <div className = "border-0 mb-3">
                            <div className = "card-body ">
                                <div className = "row">
                                    <div className = "card-icon pt-2 d-md-block ml-3">
                                    {
                                        window.innerWidth <= 500 ?
                                            <img src = { mask } className = "icons" />
                                        :
                                        window.innerWidth > 500 && window.innerWidth <= 950 ?
                                            <img src = { mask } className = "iconm" />
                                        :
                                            <img src = { mask } className = "iconl" />
                                    }
                                    </div>
                                    <div className = "col">
                                        <h3 className = "card-title h3 mb-2 text-left">Cover your mouth and nose with a cloth face cover when around others</h3>
                                        <ul>
                                            <li>You could spread COVID-19 to others even if you do not feel sick.</li>
                                            <li>Everyone should wear a cloth face cover when they have to go out in public, for example to the grocery store or to pick up other necessities.
                                                <ul>
                                                    <li>Cloth face coverings should not be placed on young children under age 2, <br /> anyone who has trouble breathing, or is unconscious, incapacitated or otherwise unable to remove the mask without assistance.</li>
                                                </ul>
                                            </li>
                                            <li>The cloth face cover is meant to protect other people in case you are infected.</li>
                                            <li>Do NOT use a facemask meant for a healthcare worker.</li>
                                            <li>Continue to keep about 6 feet between yourself and others. The cloth face cover is not a substitute for social distancing.</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className = "row ">
                    <div className = "col-md-12">
                        <div className = "border-0 mb-3">
                            <div className = "card-body ">
                                <div className = "row">
                                    <div className = "card-icon pt-2 d-md-block ml-3">
                                    {
                                        window.innerWidth <= 500 ?
                                            <img src = { sneeze } className = "icons" />
                                        :
                                        window.innerWidth > 500 && window.innerWidth <= 950 ?
                                            <img src = { sneeze } className = "iconm" />
                                        :
                                            <img src = { sneeze } className = "iconl" />
                                    }
                                    </div>
                                    <div className = "col">
                                        <h3 className = "card-title h3 mb-2 text-left">Cover coughs and sneezes</h3>
                                        <ul>
                                            <li><strong>If you are in a private setting and do not have on your cloth face covering, remember to always cover your mouth and nose</strong> <br /> with a tissue when you cough or sneeze or use the inside of your elbow.</li>
                                            <li><strong>Throw used tissues</strong> in the trash.</li>
                                            <li>Immediately <strong>wash your hands</strong> with soap and water for at least 20 seconds. If soap and water are not readily available, clean your hands with a hand sanitizer that contains at least 60% alcohol.</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className = "row ">
                    <div className = "col-md-12">
                        <div className = "border-0 mb-3">
                            <div className = "card-body bg-white">
                                <div className = "row">
                                    <div className = "card-icon pt-2 d-md-block ml-3">
                                    {
                                        window.innerWidth <= 500 ?
                                            <img src = { cleaning } className = "icons" />
                                        :
                                        window.innerWidth > 500 && window.innerWidth <= 950 ?
                                            <img src = { cleaning } className = "iconm" />
                                        :
                                            <img src = { cleaning } className = "iconl" />
                                    }
                                    </div>
                                    <div className = "col">

                                        <h3 className = "card-title h3 mb-2 text-left">Clean and disinfect</h3> <br />
                                        <ul className = "false">
                                            <li><strong>Clean AND disinfect frequently touched surfaces daily</strong>. This includes tables, doorknobs, light switches, countertops, handles, desks, phones, keyboards, toilets, faucets, and sinks.</li>
                                            <li><strong>If surfaces are dirty, clean them.</strong> Use detergent or soap and water prior to disinfection.</li>
                                            <li><strong>Then, use a household disinfectant.</strong> Most common EPA-registered household disinfectants will work.</li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className = "row ">
                    <div className = "col-md-12">
                        <div className = "border-0 mb-3">
                            <div className = "card-body bg-white">
                                <div className = "row">
                                    <div className = "card-icon pt-2 d-md-block ml-3">
                                    {
                                        window.innerWidth <= 500 ?
                                            <img src = { health } className = "icons" />
                                        :
                                        window.innerWidth > 500 && window.innerWidth <= 950 ?
                                            <img src = { health } className = "iconm" />
                                        :
                                            <img src = { health } className = "iconl" />
                                    }
                                    </div>
                                    <div className = "col">
                                        <h3 className = "card-title h3 mb-2 text-left">Monitor Your Health</h3>
                                        <ul className = "false">
                                            <li><strong>Be alert for symptoms.</strong> Watch for fever, cough, shortness of breath.
                                                <ul>
                                                    <li>Especially important if you are running essential errands, going into the office or workplace, and in settings where it may be difficult to keep a physical distance of 6 feet.</li>
                                                </ul>
                                            </li>
                                            <li><strong>Take your temperature</strong> if symptoms develop.
                                                <ul>
                                                    <li>Dont take your temperature within 30 minutes of exercising or after taking medications that could lower your temperature, like acetaminophen.</li>
                                                </ul>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Precautions;