import React from 'react';
import { HashRouter, Switch, Route, Redirect } from 'react-router-dom';
import Header from './Components/Header';
import Home from './Components/Home';
import Footer from './Components/Footer';
import World from './Components/World';
import Precautions from './Components/Precautions';
import News from './Components/News';
import './style/style.css';

function App() {
  return (
    <HashRouter>
      <div className = "page-container">
        <Header />
        <Switch>
          <Route exact path="/" render={()=>( <div className="body"> <Home /> </div> )} />
          <Route exact path='/world' render={()=>(<div className="body"> <World /> </div> )} />
          <Route exact path='/news' render={()=>(<div className="body"> <News /> </div> )} />
          <Route exact path='/precaution' render={()=>(<div className="body"> <Precautions /> </div> )} />
          <Redirect to = "/" />
        </Switch>
        <Footer />
      </div>
    </HashRouter>
  );
}


//  basename = "/covid-19-tracker"

export default App;
